	module.exports = {"data":{"markdownRemark":{"html":"<h2>“<a href=\"/filez/music/mechanism_smaf_ma2.mp3\">mechanism” 着メロ風リミックスを聞く</a> ([filesize]<a href=\"https://danmaq.com/filez/music/mechanism_smaf_ma2.mp3%5B/filesize%5D\">https://danmaq.com/filez/music/mechanism<em>smaf</em>ma2.mp3[/filesize]</a>)</h2>\n<p>ビッグビート……良く言えばロックとテクノの融合。ロックを象徴するギターとドラムのループ(ベース？、これはギターなのだ！)にテクノを象徴するスクウェア波の融合。  ドラム音をいろいろ鳴らしてたらデジロックが作りたくなって後は勢いでドラムパターンだけは作ってしまいました。しかしそれだけではなんか物足りないので前々から考えてた「うねりのある曲を作りたい！」って野望を叶えるべく、矩形波を入れた上にボリュームを引っ張ってみたのですがイマイチです……イマイチと言えば音質もイマイチです。音源が悪い(\\680で叩き売りしてたXG互換チップ)ってのもあるかもしれませんが。 この辺もまたリミックス出してみようかな、と思っているけど以下NowLoading</p>\n<h2>2009/9/7追記</h2>\n<p>BMS版はNORMAL譜面を追加し、従来譜面の難易度設定を7段階から12段階へ引きなおしました。旧難易度は5(ANOTHER:7)です。(従来譜面は殆ど弄っていません)</p>\n<ul>\n<li>bigbeat | 1:47 | 129BpM | BMSLEVEL:N5-H6-A10 | BMSNOTES:N560-H693-A977</li>\n<li>“<a href=\"/filez/music/thumb/mec_l.wma\">mechanism” を試聴する(WMA)</a> ([filesize]<a href=\"https://danmaq.com/filez/music/thumb/mec_l.wma%5B/filesize%5D\">https://danmaq.com/filez/music/thumb/mec_l.wma[/filesize]</a>)</li>\n<li><a href=\"/filez/music/mechanism.zip\">BMS “mechanism” をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/music/mechanism.zip%5B/filesize%5D\">https://danmaq.com/filez/music/mechanism.zip[/filesize]</a>)</li>\n</ul>","frontmatter":{"cover":null,"date":"2001-02-03T14:43:32+09:00","strDate":"2001/2/3","title":"[BMS] mechanism"}}},"pathContext":{"path":"/ja/blog/2001/02/03-mechanism/","langKey":"ja"}}

